











import Vue from "vue";
import {Component} from "vue-property-decorator";
import Tags from "@/components/Money/Tags.vue";
import FormItem from "@/components/Money/FormItem.vue";
import NumberPad from "@/components/Money/NumberPad.vue";
import Tabs from "@/components/Tabs.vue";
import typeList from "@/constants/typeList";


// const tagList=tagsModel.fetch();
window.localStorage.setItem("version", "0.0.2"); //进行数据库升级  数据库迁移的策略
@Component({
  components: {NumberPad, FormItem, Tags, Tabs},
  computed: {
    recordList() {
      return this.$store.state.recordList;
    },
    tags() {
      return this.$store.state.tagList;
    }
  }
}) //必须置为最后一行

export default class Money extends Vue {
  record: RecordItem = {
    tags: [],
    notes: "",
    type: "-",
    amount: 0
  };
  typeList = typeList;

  created() {
    this.$store.commit("fetchRecordList");
    this.$store.commit("fetchTagList");
  }

  onUpdateTags(tag: Tag[]) {
    if (this.record) {
      this.record.tags = tag;
    }
  }


  onUpdateAmount(number: string) { //通过$emit传出的事件都是以字符串传值。
    if (this.record) {
      this.record.amount = parseFloat(number);
    }
  }

  saveRecord() {
    if (this.record) {
      if (!this.record.tags || this.record.tags.length === 0) {
        return window.alert("请至少选择一个标签");

      }
      this.$store.commit("createRecord", this.record);
      this.record.notes = "";
      if (this.$store.state.recordError === null) {
        window.alert("添加成功");
      }
    }
  }
}
